

export interface User {
    username: string,
    favourites: string[],
    role: string,
    email: string,
    phoneNumber: string,
}



export interface AuthType {
    user: User | null;
    isAuthenticated: boolean;

}

export interface Restaurant {
    title: string,
    rating: number,
    place: string,
    categories: Omit<Category[], "All">,
    imageUrl: string,
    websiteUrl: string,
    phone: string,
    adress: string,
    description: string
}

export enum Category {
    All = "All Restaurants",
    FastFood = "Fast Food",
    FineDining = "Fine Dining",
    Healthy = "Healthy",
    Desserts = "Desserts",
}